#about-page {
  opacity: 0; 
  visibility: hidden;
  /* display: none; */
  transition: visibility 0.3s, opacity 0.3s ease-in-out; 
}


#about-page.show {
  /* display: flex; */
  visibility: visible;
  opacity: 0.8;
  cursor: pointer;
