}



@keyframes move {
  from {right: -500px}
  to {right: 0px}
}







.about-info-container {
  width: 70%;
  margin-top: 0px;
  text-align: center;
  height: 400px;
  display: flex;
  justify-content: space-between;
  flex-direction: column;

}

.about-info-container h4 {
  margin-top: 20px;
  font-size: 14px;
  line-height: 17px;
  
}

.about-info-container h3 {
  margin-bottom: 0;
  
}

