.footer {
    // background-color: green;
    max-width: 1600px;
    margin: 0 auto 0 auto;
    height: 300px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: white;
    flex-direction: column;
    width: 100%;
    // border-bottom: 1px solid white
    
}

.footer-shop-all {
//    font-size: 145px;
font-size: 80px;
   position: absolute;
   margin-top: 100px;
}

.footer-oval {
    border: 1px solid white;
    width: 280px;
    height: 100px;
    border-radius: 100%;
    margin-top: 100px;

}

.footer-separator {
    height: 1px;
    width: 100%;
    max-width: 1600px;
    margin: 0 auto 0 auto;
    background-color: white;
    margin-bottom: 40px;


}


.footer-container {
    width: 95%;
    height: 130px;
    // background-color: green;
    color: #c3c3c3;
    max-width: 1600px;
    margin: 0 auto 0 auto;
    display: none;
    justify-content: space-between;
    align-items: center;
    border-top: 1px white solid;
    font-size: 10px;

}

.footer-links-container {
    display: flex;
    justify-content: space-between;
    // width: 22%;
}

.footer-container-mobile {
    width: 95%;
    height: 70px;
    // background-color: green;
    color: #c3c3c3;
    max-width: 1600px;
    margin: 0 auto 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-top: 1px white solid;
    font-size: 10px;

}

.footer-links-container-mobile {
    display: flex;
    justify-content: space-between;
    width: 60%;
}