.product {
    max-width: 1600px;
    margin: 0 auto 0 auto;
    margin-top: 3%;
    display: flex;
    flex-direction: column;
}

.product-hero-container {
    width: 100%;
    display: flex;
    justify-content: space-between;
    // border: 1px solid orange;
    margin-bottom: 220px;
}

.product-image-container {
    width: 800px;
    height: 800px;
    background-color: white;
    border-radius: 12px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.product-hero-info {
    color: #c3c3c3;
    width: 50%;
    margin-left: 3%;
}

.product-hero-productinfo-container {
    // border: 1px solid orangered;
    
}

.product-hero-price-container {
    display: flex;
}

.product-hero-price-container h5 {
    display: flex;
    margin-left: 30px;
}

.product-hero-size-container {
    display: flex;
}

.product-size-selector-container {
    display: flex;
    margin-left: 39px;
    width: 200px;
    justify-content: space-between;
}

.product-hero-info-container {
    display: flex;

}

.product-hero-info-container p {
    // width: 200px;
    margin-left: 36px;
}



.product-otherproducts-container {
    display: flex;
    justify-content: space-between;
    color: #c3c3c3;
    margin-bottom: 220px;
}


.product-otherproducts-slide1-container {
    width: 390px;
    height: 390px;
    background-color: white;
    border-radius: 12px;
}

.product-otherproducts-imagecontainer {
    display: flex;
    width: 1220px;
    justify-content: space-between;
    // background-color: grey;
}

.product-otherproducts-info {
    width: 390px;
    margin-right: 40px;
}

.product-otherproduct1-image {
    height: 80%;
    width: 100%;
    border-bottom: 1px solid black;
}

.product-otherproduct1-info {
    display: flex;
    justify-content: space-between;
    width: 96%;
    margin: 0 auto 0 auto;
    color: black;
}